.elementWithLinkForPorfolio {
    position: relative;
    width: 400px;
    height: 500px;
    margin-bottom: 100px;
    box-shadow: 10px 5px 38px #b6a8a8;
    cursor: pointer;
    border-radius: 25px;
}

.imageFromElement {

    width: 400px;
    height: 400px;
}

.spanTitle {
    position: absolute;
    left: 5px;
    margin-top: 22px;
    font-size: 20px;
    font-family: Arial, Helvetica, sans-serif;
}

img {
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    width: 400px;
    height: 400px;
}